import Renderer from "./Renderer.js"
import Ico from './Ico'
import Camera from './Camera'
import { PerspectiveCamera, Vector3, Scene, AxesHelper, GridHelper } from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'


export default class MainMulticamera {
    constructor(container) {
        // ta funkcja umożliwia dodawania kolejnych kamer
        this.scene = new Scene()
        this.renderer = new Renderer(container)
        this.renderer.autoClear = false


        // camera 1
        this.camera1 = new Camera(3, window.innerWidth, window.innerHeight);
        this.camera1.position.set(100, 100, 100)
        this.camera1.lookAt(new Vector3(0, 0, 0));

        // camera 2
        this.camera2 = new Camera(0.1, window.innerWidth, window.innerHeight);
        this.camera2.position.set(100, 100, 200)
        this.camera2.lookAt(new Vector3(0, 0, 0));
        // camera 3
        this.camera3 = new Camera(3, window.innerWidth, window.innerHeight);
        this.camera3.position.set(0, 500, 0)
        this.camera3.lookAt(new Vector3(0, 0, 0));
        // camera 4
        this.camera4 = new Camera(3, window.innerWidth, window.innerHeight);
        this.camera4.position.set(200, 0, 200)
        this.camera4.lookAt(new Vector3(0, 0, 0));


        //ico
        this.ico = new Ico()
        this.scene.add(this.ico)
        const controls = new OrbitControls(this.camera1, this.renderer.domElement);
        var axes = new AxesHelper(1000)
        this.scene.add(axes)
        const gridHelper = new GridHelper(100, 10);
        this.scene.add(gridHelper)

        this.render();
    }

    render() {

        this.ico.update() // obrót ico
        this.renderer.setViewport(0, 0, innerWidth / 2, innerHeight / 2);
        this.renderer.render(this.scene, this.camera1);
        // kolejne viewporty dla kolejnych kamer
        this.renderer.setViewport(0, 1080 / 2, innerWidth / 2, innerHeight / 2);
        this.renderer.render(this.scene, this.camera2);
        this.renderer.setViewport(1920 / 2, 0, innerWidth / 2, innerHeight / 2);
        this.renderer.render(this.scene, this.camera3);
        this.renderer.setViewport(1920 / 2, 1080 / 2, innerWidth / 2, innerHeight / 2);
        this.renderer.render(this.scene, this.camera4);

        requestAnimationFrame(this.render.bind(this));
    }
}
